
td a {
    text-decoration: none;
    color: white;
}

td a:hover{
    color: var(--hover-color);
}

table {
    display: table;
    width: 95%;
    margin: 0 auto;
    border-collapse: collapse;
}

.table-headers {
    text-align: left;
    border-bottom: solid white 2px;
}

.table-header-right {
    border-right: solid white 2px;
}
  